import request from '../../util/request'
import common from '../../util/common'

export default {
    name: 'My',
    data () {
        return {
            isDark: false,
            shareModal: false,
            settingModal: false,
            sortModal: false,
            clipboard: '我正在用畅游看吧读免费百万小说。下载地址：git@github.com:songning123456/cykb-wx.git',
            displayInfo: [
                {
                    icon: 'appreciate',
                    title: '请给我好评'
                },
                {
                    icon: 'share',
                    title: '分享给朋友'
                },
                {
                    icon: 'comment',
                    title: '意见反馈'
                },
                {
                    icon: 'settings',
                    title: '设置'
                }
            ],
            settings: [
                {
                    icon: 'arrow',
                    title: '退出登录',
                    type: 'exit'
                }, {
                    icon: 'text',
                    title: '书架排序',
                    type: 'sort'
                }, {
                    icon: 'switch',
                    title: '夜间模式',
                    type: 'nightMode'
                }
            ],
            sorts: [
                {
                    icon: 'cuIcon-read',
                    title: '最近阅读'
                },
                {
                    icon: 'cuIcon-time',
                    title: '更新时间'
                },
            ]
        }
    },
    computed: {
        userInfo () {
            return this.$store.state.userInfo
        },
        sortType () {
            return this.$store.state.sortType
        },
        sexClazz () {
            let clazz = ''
            if (this.userInfo) {
                if (this.userInfo.gender === 1) {
                    clazz = 'cuIcon-male bg-blue'
                } else if (this.userInfo.gender === 2) {
                    clazz = 'cuIcon-female bg-pink'
                }
            }
            return clazz
        },
        avatarClazz () {
            let clazz = 'background-image:url(\'data:image/svg+xml;base64,\');'
            if (this.userInfo && this.userInfo.avatar) {
                clazz = `background-image:url(${this.userInfo.avatar})`
            }
            return clazz
        }
    },
    methods: {
        tapBtn (type) {
            switch (type) {
                case 'appreciate':
                    break
                case 'share':
                    this.shareModal = true
                    break
                case 'comment':
                    uni.navigateTo({
                        url: '/pages/comment/Comment'
                    })
                    break
                case 'settings':
                    this.settingModal = true
                    break
            }
        },
        settingBtn (type) {
            switch (type) {
                case 'exit':
                    uni.showLoading({
                        title: '注销中'
                    })
                    try {
                        uni.clearStorageSync()
                        this.$store.state.userInfo = null
                    } catch (e) {
                        console.error(e)
                    } finally {
                        common.sleep(500)
                        uni.hideLoading()
                    }
                    break
                case 'sort':
                    this.sortModal = true
                    break
                case 'nightMode':
                    break
            }
        },
        sortBtn (e) {
            this.$store.commit('SET_SORTTYPE', e.currentTarget.dataset.sort)
            uni.setStorage({
                key: 'sortType',
                data: e.currentTarget.dataset.sort
            })
            this.sortModal = false
        },
        nightModeBtn (e) {
            this.isDark = e.detail.value
        },
        hideShareModal (type) {
            this.shareModal = false
            if (!type) {
                // 复制到剪切板
                uni.setClipboardData({
                    data: this.clipboard,
                    success: function () {
                        console.log('success')
                    }
                })
            }
        },
        hideSettingModal () {
            this.settingModal = false
        },
        hideSortModal () {
            this.sortModal = false
        },
        wxBtn () {
            uni.login({
                success: response2 => {
                    // 获取用户信息
                    uni.getUserInfo({
                        provider: 'weixin',
                        success: response3 => {
                            uni.showLoading({
                                title: '登陆中'
                            })
                            let params = {
                                condition: {
                                    code: response2.code,
                                    avatar: response3.userInfo.avatarUrl,
                                    nickName: response3.userInfo.nickName,
                                    gender: response3.userInfo.gender
                                }
                            }
                            request.post('/users/weixin/getUsersInfo', params).then(data => {
                                uni.hideLoading()
                                if (data.status === 200) {
                                    let result = JSON.stringify(data.data[0])
                                    uni.setStorage({
                                        key: 'userInfo',
                                        data: result
                                    })
                                    this.$store.commit('SET_USERINFO', data.data[0])
                                    this.$emit('userInfo', data.data[0])
                                } else {
                                    uni.showToast({
                                        title: '获取用户信息失败',
                                        duration: 1000,
                                        icon: 'none'
                                    })
                                }
                            }).catch(e => {
                                uni.hideLoading()
                                uni.showToast({
                                    title: '获取用户信息失败',
                                    duration: 1000,
                                    icon: 'none'
                                })
                            })
                        },
                        fail: reject3 => {
                            debugger;
                            // doNothing...
                        }
                    })
                },
                fail: response2 => {
                    debugger;
                    uni.showToast({
                        title: '登录失败',
                        duration: 1000,
                        icon: 'none'
                    })
                }
            })
        }
    }
}
